/*
z-index guide
0: 
1: 
2: DEFAULT
3: Popout
4: Setting Widget
5: Widget when dragging
6: Drag icon
*/

:root{
    --randColor: ;
    --randColorOpacity: ;
}

body{
    margin: 0rem;
}

/// Class: fonts
.font{
    color: var(--randColor);
    &.large{
        font-size: 1.75em;
    }
    &.medium{
        font-size: 1.17em;
    }
    &.small{
        font-size: 0.8em;
    }
    &.smaller{
        font-size: 0.7em;
    }
    &.normal{
        font-weight: normal;
    }
    &.bold{
        font-weight: bold;
    }
    &.normal-transparent{
        font-weight: normal;
        color: rgba(var(--randColorOpacity), 0.4);    
    }
    &.bold-transparent{
        font-weight: bold;
        color: rgba(var(--randColorOpacity), 0.4);
    }
    &.break-word{
        word-break: break-all;
    }
}

.quote{
    margin: 0rem 0.6rem;
    font-weight: bold;
    color: var(--randColor);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &.large{
        font-size: 3.5em;
    }
}

.author{
    font-size: 0.9em;
    text-align: right;
    font-style: italic;
    margin: 0.6rem 1.85rem 0.94rem 0rem;
    color: var(--randColor);
}

/// Class: design
.widget{
    z-index: 2;
    position: absolute;
}

.widget-animation{
    background-color: white;
    padding: 1.6rem;
    border-radius: 25.4px;
    border: 1px solid var(--randColor);
}

.draggable{
    z-index: 6;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--randColor);
    filter: brightness(70%);
}

.cut-scrollbar-corner-part-1{
    max-width: 23.2em;
    overflow: hidden;
    border: 1px solid var(--randColor);
    border-radius: 15px;
    &.textarea{
        height: 4.8em;
    }
    &.p{
        height: 8.4em;
    }
}

.cut-scrollbar-corner-part-2{
    max-width: 30.1em;
    padding: 0rem;
    margin: 0rem;
    text-align: center;
    overflow-y: scroll;
    overflow-x: hidden;
    font-size: 0.948em;
    &.textarea{
        height: 6.3em;
        max-width: 30.1em;
        width: calc(100vmin - 4.05em);
        border: none;
        resize: none;
        outline: none;
    }
    &.p{
        height: 9em;
        word-wrap: break-word;
        &.short{
            height: 6.5em;
        }
    }
}

.btn-match{
    padding: 0.47rem;
    border: none;
    border-radius: 9px;
    color: white;
    background-color: var(--randColor);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &:hover{
        cursor: pointer;
        background-color: rgba(var(--randColorOpacity), 0.8);
    }
    &:active{
        transform: scale(0.98);
    }
    &.option{
        padding: 0.2rem 0.4rem;
        border-radius: 6.6px;
        font-size: 0.76em;
        &.opt-long{
            max-width: 25em;
            min-width: 5em;
            width: calc(100vmin - 4em);
        }
        &.opt-medium{
            max-width: 12.3em;
            min-width: 3.7em;
            // width: calc(100vmin - 4em);
            width: calc(100vmin - 14em);
        }
        &.opt-small{
            width: 8em;
        }
    }
    &.inverse{
        color: var(--randColor);
        background-color: white;
        padding: 0rem 0.35rem;
        &:hover{
            color: rgba(var(--randColorOpacity), 0.8);
            background-color: unset;
        }
        &:active{
            transform: scale(0.93);
        }
        &.inv-small{
            font-size: 0.95em;
        }
    }
    &.fadded{
        color: rgba(var(--randColorOpacity), 0.08);
        background-color: rgba(var(--randColorOpacity), 0.08);
        &:hover{
            color: white;
            background-color: rgba(var(--randColorOpacity), 0.5);
        }
        &.inverse{
            color: rgba(var(--randColorOpacity), 0.2);
            background-color: unset;
            &:hover{
                color: rgba(var(--randColorOpacity), 0.5);
                background-color: unset;
            }
        }
    }
}

.select-match{
    cursor: pointer;
    color: var(--randColor);
    border: 1px solid var(--randColor);
    border-radius: 5.6px;
    font-size: 0.75em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    padding: 0.05rem 1.12rem 0.05rem 0.3rem;
    background-image: url("./imgs/downArrow.svg");
    background-repeat: no-repeat;
    background-position: right 0.3em top 0.12em;
    background-size: 1em 1.1em;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    :checked{
        background-color: rgba(var(--randColorOpacity), 0.3);
    }
}

.btn-ends{
    display: flex;
    justify-content: space-between;
    margin: 0rem 1.5rem;
}

.when-elements-are-not-straight{
    display: inline-flex;
}

.center-flex{
    display: flex;
    justify-content: center;
    align-items: center;
    &.only-justify-content{
        align-items: unset;
    }
    &.only-align-items{
        justify-content: unset;
    }
}

.popout{
    z-index: 3;
    visibility: hidden;
    position: absolute;
    background-color: white;
    font-size: 0.76em;
    text-align: left;
    border-radius: 20px;
    box-shadow: 0em 0em 0.6em 0.05em var(--randColor);
}

.pointer-events-none{
    pointer-events: none;
}

.input-typable{
    border: 1px solid var(--randColor);
    &:focus{
        outline: none;
    }
    &.no-side{
        border: none;
        text-align: right;
        padding: 0;
    }
    &.all-side{
        border-radius: 5.6px;
        margin: 0.2em;
        &.input-button-input{
            font-size: 0.8em;
            width: 13.8em;
            margin: 0rem 0.3rem 0.2rem;
            padding: 0.15rem 0.15rem;
        }
    }
    &.right-side{
        padding: 0.05rem 0.15rem;
        border-right: none;
        border-radius: 5.6px 0px 0px 5.6px;
        &.with-help-btn{
            height: 2.05em;
            max-width: 15.5em;
            width: 100vmin;
        }
    }
}

.help-btn{
    height: 2.3em;
    max-width: 2.1em;
    width: 100vmin;
    padding: 0.35rem;
    background-color: white;
    border: 1px solid var(--randColor);
    align-items: center;
    cursor: pointer;
    &.left-side{
        border-left: none;
        border-radius: 0px 5.6px 5.6px 0px;    
    }
}

.option{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.31em;
    gap: 0.3em;
}

.option-item{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 0.3em;
}

.space-nicely{
    &.top{
        margin: 0.9rem 0rem 0.3rem;
    }
    &.right{
        &.short{
            margin-right: 0.15rem;
        }
    }
    &.bottom{
        margin-bottom: 0.6rem;
        &.short{
            margin-bottom: 0.25rem;
        }
    }
    &.left{
        &.short{
            margin-left: 0.15rem;
        }
    }
    &.all{
        margin: 0.6rem;
        &.long{
            margin: 1rem;
        }
    }
}

.disabled-option{
    opacity: 0.5;
}

.doesnt-work{
    background-color: rgb(255, 0, 0);
    &:hover{
        cursor: not-allowed;
        background-color: rgb(255, 0, 0);
    }
    &:active{
        transform: none;
    }
}

.no-select{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.bottom-left{
    position: absolute;
    top: 21em;
    left: 3em;
}

.bottom-right{
    position: absolute;
    top: 20.2em;
    right: 3.7em;
}

/// IDs
#App{
    position: relative;
    font-family: 'Tahoma', sans-serif;
    background-color: var(--randColor);
    overflow: hidden;
}

#widget-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

#settings-box{
    z-index: 4;
    max-width: 100vmin;
    max-height: 26.46em;
    #settings-box-draggable{
        visibility: hidden;
    }
    #settings-box-animation{
        #show-hide-widgets-popout{
            padding: 0.6rem;
        }
    }
}

#quote-box{
    max-width: 50em;
    max-height: 26.46em;
    text-align: center;
    #quote-box-draggable{
        visibility: hidden;
    }
    #quote-box-animation{
        padding: 0.3rem 1.9rem 1.58rem;
    }
    #quote{
        display: flex;
        flex-direction: row;
        margin-top: 1rem;
        #quote-text{
            padding-top: 0.6rem;
        }
    }
}

#translator-box{
    font-size: 1.11em;
    max-width: 26.24em;
    width: 100vmin;
    #translator-box-draggable{
        visibility: hidden;
    }
    #translator-box-animation{
        #replace-popout{
            visibility: hidden;
            height: 6em;
            width: 28em;
            padding: 0.35rem 0rem;
        }
        #reverse-popout{
            visibility: hidden;
            height: 6em;
            width: 28em;
            padding-top: 0.25rem;
        }
        #case-transform-popout{
            visibility: hidden;
        }
    }
    #translator-preview-cut-corner{
        margin-top: 0.9rem;
    }
}

#google-translator-box{
    font-size: 1.11em;
    max-width: 26.24em;
    width: 100vmin;
    #google-translator-box-draggable{
        visibility: hidden;
    }
    #google-translator-preview-cut-corner{
        margin-top: 0.9rem;
    }
}

#calculator-box{
    font-size: 1.11em;
    #calculator-box-draggable{
        visibility: hidden;
    }
    #calculator-display-container{
        display: flex;
        flex-direction: column;
        width: 9.5em;
        margin: 0rem 0rem 0.3rem;
    }
    #calculator-btn-container{
        display: grid;
        grid-template-columns: repeat(4, 2.225em);
        gap: 0.27em;
        #calculator-btn-memory-display{
            visibility: hidden;
            position: absolute;
            top: 34%;
            left: 1%;
            width: 12.5em;
            height: 12.7em;
            background-color: white;
            border-radius: 0px 0px 23px 23px;
            #calculator-btn-memory-container{
                display: flex;
                flex-direction: column;
                width: 90%;
                height: 70%;
                margin: 1.23rem 0.3rem 1.23rem 0.9rem;
                overflow-y: scroll;
                overflow-x: hidden;
                p{
                    margin: 0rem 0.3rem 0.6rem;
                    text-align: right;
                    word-wrap: break-word;
                }
            }
            #calculator-btn-memory-display-close{
                position: absolute;
                top: -51.1%;
                left: -1%;
                width: 16.9em;
                height: 51.1%;
                border: none;
                border-radius: 23px 23px 0px 0px;
                background-color: rgba(0, 0, 0, 0.2);
                cursor: pointer;
            }
            #calculator-btn-trash{
                position: absolute;
                bottom: 0.7em;
                right: 0.3em;
                background-color: transparent;
            }
        }
    }
    #calculator-input-expand-popout{
        height: 10em;
        width: 10em;
    }
}

#weather-box{
    max-width: 22.1em;
    width: 100vh;
    #weather-box-draggable{
        visibility: hidden;
    }
    #weather-box-animation{
        #weather-search-container{
            gap: 0.3em;
            height: 2.17em;
            width: 18.76em;
            #weather-search-help-container{
                padding: 0.5em 0em 1em;
                ul{
                    height: 12.64em;
                    width: 18em;    
                    margin: .7rem 2.35rem;
                    padding: 0rem;
                }
            }
        }
        #weather-info-container{
            display: flex;
            flex-direction: column;
            height: 12.47em;
            max-width: 18.76em;
            width: calc(100vh - 3.5em);
            border: 1px solid var(--randColor);
            border-radius: 7.5px;
            margin: 0.9rem 0rem 0rem;
            word-break: break-all;
            #weather-info-icon-temp{
                display: flex;
                flex-direction: row;
                align-items: center;
                #weather-info-temp-c-container{
                    display: flex;
                    flex-direction: column;
                    margin-right: 1.9rem;
                }
                #weather-info-temp-f-container{
                    display: flex;
                    flex-direction: column;
                }
            }
            #weather-info-cond-local-time{
                display: flex;
                flex-direction: row;
                gap: 1.8em;
                margin: 0rem 0.76rem;
            }
            #weather-info-wind{
                display: flex;
                flex-direction: row;
                gap: 2em;
                margin: 0.6rem 0.76rem 0rem;
            }
            #weather-info-location{
                width: 18.23em;
                position: absolute;
                bottom: 2.4em;
                right: 2.4em;
                word-wrap: break-word;
                text-align: right;
            }
        }
        #weather-last-updated{
            position: absolute;
            bottom: 0.7em;
            left: 2.4em;
        }
    }
}

